import React, { Component } from 'react';
import Styles from "./tablec-css-modules.module.css";
class tablec  extends Component{
    constructor(props){
        super(props)

        this.state ={
            shown: true,
        }
    }
   // <h2 style={ shown }>this.state.shown = true</h2>
   // <h2 style={ hidden }>this.state.shown = false</h2>
	toggle() {
		this.setState({
			shown: !this.state.shown
		});
    }
    
    render() {
        const {  headers } = this.props;

		var shown = {
			display: this.state.shown ? "block" : "none"
		};
		
		var hidden = {
			display: this.state.shown ? "none" : "block"
		}
		
		return (
			<div style={{background: `pink`, float: `right`, border: `none`,paddingRight: this.state.shown ?`7%`:`40%`}}>
				
				<button onClick={this.toggle.bind(this)} style={{ position: `fixed`, float: `right`,zIndex: `1`, backgroundColor: `#e7e7e7`, fontSize: `12px`, borderRadius: `12px`}}>{this.state.shown ? "<<" : "Hide"} </button>
                <div className={Styles.toc_container} style={shown ? hidden:shown}>
                    <p className={Styles.toc_title}></p>
                    <p className={Styles.toc_title}></p>
                    <br/>
                    <span className={Styles.toc_title}> &nbsp;Contents</span>
                    <ul className={Styles.toc_list}>
                        {
                            headers.map((item)=>{
                                return(
                                    <li><a href={`#${item.id}`}>{item.name}</a>&nbsp;</li>
                                );
                            
                            })  
                        }

                    </ul>
                </div>
			</div>
		)
	}
}
export default tablec